.icon {
  width: 24px;
  height: 24px;
  display: block;
  background-size: cover;

  @include onTablet {
    width: 32px;
    height: 32px;
  }

  &--phone {
    position: relative;
    background-image: url(../images/icons/icon-phone-call.svg);

    @include onDesktop {
      &::before {
        content: "+1 234 555-55-55";
        position: absolute;
        left: -148px;
        padding: 8px 0;
        align-self: center;
        color: $main-text-color;
        font-family: "Inter", "Manrope", sans-serif;
        font-size: 12px;
        font-weight: 700;
        line-height: 16px;
        letter-spacing: 2px;
        opacity: 0;
      }

      &:hover {
        &::before {
          @include addTransition;
          opacity: 1;
        }
      }
    }
  }

  &--menu {
    background-image: url(../images/icons/icon-burger-menu.svg);

    &:hover {
      background-image: url(../images/icons/icon-burger-menu-hover.svg);
    }
  }

  &--close {
    background-image: url(../images/icons/icon-burger-menu-close.svg);
  }
}
